.home {
    height: 100vh;
    width: 100vw;
    overflow-x: hidden;
    border: 3px solid red;


    .homeContainer {
        background-color: aliceblue;
        padding: 70px;
        width: 100%;
        height: 100%;

        h1 {
            color: darkslateblue;
        }

        h3 {
            
        }

        .page{
            width: 80vw;
            margin: 70px 0;
            padding: 40px;

            .statusDots{
                margin: 20px 10px 0 0;
                display: flex;
                gap: 15px;

                .dot{
                    height: 20px;
                    width: 20px;
                    background-color: green;
                    border-radius: 20px;

                    &.red{
                        background-color: red;
                    }
                    &.green{
                        background-color: green;
                    }
                }
                .text{
                    color: gray;
                }
            }


            button{
                background-color: transparent;
                border-radius: 4px;
                color: rgb(122, 43, 226);
                border: 1px solid rgb(122, 43, 226);
                padding: 10px 30px;
                margin: 40px 20px 20px 0;
                cursor: pointer;

                
                &:disabled{
                    color: rgb(185, 146, 235);
                    cursor: unset;

                }

                &:hover:enabled{
                    background-color: rgb(122, 43, 226);
                    color: white;
                }
                
                &.primary{
                    background-color: rgb(122, 43, 226);
                    color: white;
                    
                    &:hover:enabled{
                        background-color: rgb(90, 20, 181);
                        color: white;
                    }

                    &:disabled{
                        background-color: rgb(150, 90, 230);
                        color: rgb(177, 137, 228);
                    }
                }

                &.finish{
                    background-color: rgb(245, 62, 153);
                    color: white;
                    border: 1px solid rgb(245, 62, 153);
                    margin-left: 170px;

                    &:hover:enabled{
                        background-color: rgb(243, 34, 139);
                        color: white;
                    }

                    &:disabled{
                        color: rgb(246, 135, 190);
                        cursor: unset;

                    }
                }
            }

            .bottomActionPanel{
                display: flex;
                align-items: flex-end;
                justify-content: flex-end;
            }
        }
    }
}